body {    
  color: black;
  padding: 10px;
  font-family: Arial;
  text-align: center;
  margin: 0px;
  padding: 0px;
}

h1 {
  padding-bottom: 50px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color:gray;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


table {   
  border: 5px solid #282c34;
  width: 1000px;
  height: 200px ;
} 
caption {
  padding: 20px;
}
th  {  
  background-color: green;
  border: 5px solid black ;
  color: white;
  text-align: left; 
}
td {
  background-color: white;
  border: 5px solid black ;
}

tr {
  text-align: center;
}

legend {
  color: black;
  padding: 10px;
  font-family: Arial;
  text-align: center;
}

fieldset {
  color: black;
  padding: 10px;
  font-family: Arial;
  text-align: center;
}

label, button, input { 
  border-color:black;
  color: black;
  font-family: Arial;
  text-align: center;
}


